@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Source Sans Pro", sans-serif;
}

.todo {
  background: #002333;
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  display: grid;
  place-items: center;
}

.todo_wrapper {
  max-width: 500px;
}
figcaption {
  color: #fff;
  margin-top: 10px;
}
.input_wrapper {
  border: none;
  background: #fff;
  padding: 10px;
  min-width: 350px;
  border-radius: 4px;
  margin-top: 15px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
}
input {
  border: none;
  width: 100%;
  font-size: 15px;
  font-weight: 900;
}
input:focus-visible {
  outline: none;
  box-shadow: none;
}
i.fa.fa-plus.add-btn,
i.far.fa-trash-alt.add-btn,
i.far.fa-edit.add-btn {
  cursor: pointer;
}

i.fa.fa-plus.add-btn:hover {
  color: green;
}
i.far.fa-trash-alt.add-btn:hover {
  color: red;
}
.Inner:hover i.far.fa-trash-alt.add-btn {
  color: red;
}
.Inner:hover i.far.fa-edit.add-btn {
  color: #00ff84;
}
i.far.fa-edit.add-btn:hover {
  color: #00ff84;
}
.Inner {
  background: #00ff84;
  padding: 10px;
  min-width: 350px;
  color: #002333;
  border-radius: 4px;
  margin-top: 6px;
  display: flex;
  justify-content: space-between;
}
.Inner:hover {
  background: #fff;
}

button.remove_all_btn {
  margin-top: 40px;
  padding: 10px 35px;
  font-size: 15px;
  font-weight: 900;
  color: #616161;
  border-radius: 2px;
  border: none;
  min-width: 170px;
  cursor: pointer;
  position: relative;
  min-height: 40px;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
button.remove_all_btn:hover {
  background: #00ff84;
}
button.remove_all_btn::before,
button.remove_all_btn::after {
  content: "Check List";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  line-height: 40px;
  top: 0;
  transition: all 0.1s ease-in-out;
}
button.remove_all_btn:hover::before {
  top: -50px;
}

button.remove_all_btn::after {
  content: "Remove All";
  position: absolute;
  top: 50px;
}
button.remove_all_btn:hover::after {
  top: 0;
  color: #002333;
}
img.admin-img {
  margin-bottom: 30px;
}

@media (max-width: 480px) {
  .input_wrapper,
  .Inner {
    min-width: 300px;
    max-width: 300px;
  }
}
